<!--  -->
<template>
<div class='BookCity'>
    <!-- 搜索 -->
   <div class="searchbox">
    <div class="gender">
    <img src="@/assets/nan.png" alt="">
    </div>
    <van-search
     v-model="searchValue"
     right-icon="search" 
     left-icon=""
     shape="round"
     placeholder="请输入搜索关键词"
     @focus="inputing"
      />
   </div>

   <!-- 类别 -->
   <div class="tab">
    <van-tabs v-model="activeIndex" animated sticky="true">
      <van-tab :title="Category.name" v-for="(Category,index) in CategoryData" :key="index">
      <city-type :url="Category.url" 
       :i="index"></city-type>
      </van-tab>
    </van-tabs>
   </div>
</div>
</template>

<script>
import CityType from '@/components/CityType.vue'

export default {
  name:'BookCity',
data() {
//这里存放数据
return {
  searchValue:'',
  CategoryData:[],
  activeIndex:0,
};
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//方法集合
methods: {
   async getCategoryData(){
   await this.axios.get('/store/navi.json?gender=1').then(re=>{
     this.CategoryData=re.navi;
     console.log("this.CategoryData",this.CategoryData);
     })
   },
   inputing(){
      this.$router.push({
      name:"SearchView",
    })
   }
},
components: {
  CityType
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {
  this.getCategoryData();
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang="scss" scoped>

.searchbox{
  display: flex;
  align-items: center;
  padding: 10px;
  .gender{
   width: 20px;
   height: 20px;
   padding: 0px 10px 0 0;
   border-radius: 50%;
  }
  img{
   border-radius: 50%;
  }
}
.van-search {
  flex: 5;
  padding: 0;
}
</style>