</template>

<script>
import BookList from "./BookList.vue";
import MyCarousel from "./MyCarousel.vue";
import RankList from "./RankList.vue";

export default {
  name: "CityType",
  props: ["url", "i"],
  data() {
    //这里存放数据
    return {
      typeData: [],
      showlist: [],
      bookData1: [],
      bookData2: [],
      bookData3: [],
      bookData4: [],
      bookData5: [],
      bookData6: [],
      bookData7: [],
      bookData8: [],

      rankData1: [],
      rankData2: [],

      
    };
  },
  //监听属性 类似于data概念

  //监控data中的数据变化
  watch: {},
  //方法集合
  methods: {
    async getTypeData() {
      await this.axios.get(`${this.url}`).then((re) => {
        this.typeData = re.list;
        this.bookData1 = this.typeData[3];
        // console.log('this.bookData1',this.bookData1);
        this.bookData2 = this.typeData[5];
        this.bookData3 = this.typeData[6];
        this.bookData4 = this.typeData[8];
        this.bookData5 = this.typeData[9];
        this.bookData6 = this.typeData[10];
        this.bookData7 = this.typeData[1];
        this.bookData8 = this.typeData[2];

        this.rankData1 = this.typeData[4];
        this.rankData2 = this.typeData[7];

        //   console.log('this.booklist',this.booklist);
        console.log("this.typeData", this.typeData);
        if (this.i === 0||this.i === 4) {
          this.showlist = this.typeData[2].list.splice(1);
          console.log(this.showlist);
        } else if (this.i === 1||this.i === 5) {
          this.showlist = this.typeData[1].list.splice(1);
        }
      });
    },
     getSecid(id){
      this.getCatalogData(id);
    },
    getRankType(url,i){
      if(i===0||i===1||i===2){
          this.$router.push({
            name:'AllRankList',
            params:{
              url,i
            }
          })
      }
    },
    getCatalogData(id){
       this.axios.get("https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=" + id).then(re=>{
           this.CatalogData=re.catalog;
           let sid = this.CatalogData[0].secId
          let index = 1;
          this.$router.push({name:'ReadContent',params:{id,sid,index}})
       }) 
    },
    getBookId(id) {
      if(this.i===5){
        this.$router.push({ name: "ListenBook", params: {id} });
      }
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  computed: {},
  created() {
    this.getTypeData();
    console.log("this.i", this.i);
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted() {},
  beforeCreate() {}, //生命周期 - 创建之前
  beforeMount() {}, //生命周期 - 挂载之前
  beforeUpdate() {}, //生命周期 - 更新之前
  updated() {}, //生命周期 - 更新之后
  beforeDestroy() {}, //生命周期 - 销毁之前
  destroyed() {}, //生命周期 - 销毁完成
  activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
  components: {
    BookList,
    MyCarousel,
    RankList,
  },
};
</script>
