<!-- 复用-书籍列表 -->
<template>
 <div class="BookList">
     <div class="module-title">{{bookData.name}}</div> 
    <div class="bookList">
          <div class="bookList-box" v-for="(book,index) in bookData.list" :key="index" @click="getSecid(book.id)"
          >
            <div class="bookList-img">
               <img :src="book.cover" alt="">
             </div>
             <div class="bookList-content">
                 <div class="book-title">
                     {{book.title}}
                 </div>
                 <div class="book-author">
                     {{book.author}} / {{book.category}}
                 </div>
                 <div class="book-content">
                     {{book.content}}
                 </div>
             </div>
           </div>
          </div>
          <van-divider :style="{ padding: '0 10px' }" />
    </div>
</template>

<script>

export default {
    name:'BookList',
props:{
bookData:{
    default:Array,
},
i:{
  default:Number,
}
},
data() {
//这里存放数据
return {
     CatalogData:[],
};
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//方法集合
methods: {
     getSecid(id){
      if(this.i===5){
        this.$router.push({ name: "ListenBook", params: {id} });
      }else{
        this.getCatalogData(id);
      }
    },
    getCatalogData(id){
       this.axios.get("https://apis.netstart.cn/yunyuedu/reader/book/info.json?source_uuid=" + id).then(re=>{
           this.CatalogData=re.catalog;
           let sid = this.CatalogData[0].secId
          let index = 1;
          this.$router.push({name:'ReadContent',params:{id,sid,index}})
       }) 
    }
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发

components: {},
}
</script>
<style lang='scss' scoped>
.module-title{
     font-weight: bold;
     padding-top: 10px;
     padding-bottom: 20px;
     font-size: 16px;
 }
.bookList {
  .bookList-box {
      height: 20vh;
      width: 100vw;
      display: flex;
    // padding: 10px 0;
    .bookList-img {
        height: 17.5vh;
        width: 120px;
      box-shadow: 0 4.5px 5.5px #000202;
      img {
       height: 100%;
      }
    }

    .bookList-content {
       margin-left: 3.5vw;
        height: 115px;
        width: 95vw;
        display: flex;
        flex-direction: column;
        padding-top: 10px;
      .book-title {
        font-size: 14px;
        font-weight: bold;
      }

      .book-author {
        font-size: 12px;
        padding: 10px 0;
      }

      .book-content {
          height: 60px;
          width: 70vw;
         font-size: 12px;
         overflow : hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3; /* 限制在一个块元素显示的文本的行数 */
      -webkit-box-orient: vertical; /* 垂直排列 */
      word-break: break-all;  /* 内容自动换行 */
        line-height: 20px;
      }
    }
  }
}
</style>